.services {
  /* border: 2px solid black; */
}

.center {
  text-align: center;
}

.servicesContainer {
  display: flex;
  /* border: 2px solid; */
  width: 70%;
  margin: 0 auto;
}

.serviceContainer {
  /* border: 2px solid green; */
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 1rem;
}

.servicesContainer img {
  width: 20rem;
  height: 13.75rem;
  margin-bottom: 1rem;
}

.servicesContainer p {
  text-align: center;
}

@media (min-width: 320px) and (max-width: 500px) {
  .servicesContainer {
    flex-direction: column;
  }
}
