.inside-EachPlantComp {
  width: 233px;
  margin: 0 50px;
}
.pic-plant--EachPlantComp {
  width: 233px;
  height: 305px;
}
.pic-plant--EachPlantComp img {
  width: 233px;
  height: 257px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}
.price-EachPlantComp {
  background: #144d14;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: white;
  padding-left: 0.5rem;
  padding-right: 0.5rem;

  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}
.price-EachPlantComp p {
  color: #fff;
  text-align: center;
  font-family: Inter;
  font-size: 15px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-transform: capitalize;
  padding-top: 0.5rem;
  max-width: 173px;
}
.rating-EachPlantComp img {
  width: 112px;
  height: 28px;
  display: flex;
  margin-left: auto;
  margin-right: auto;
}
.rating-EachPlantComp p {
  color: #000;
  text-align: center;
  font-family: Inter;
  font-size: 14px;
  font-style: italic;
  font-weight: 400;
  margin-top: 0.3rem;
  line-height: normal;
}
.btns-EachPlantComp {
  display: flex;
  flex-direction: row;
  gap: 5px;
  margin-top: 2rem;
}
.view-details-btns-EachPlantComp {
  /* background: #165315; */
  width: 114px;
  display: flex;
  justify-content: center;
  height: 32px;
  align-items: center;
  border: 1px solid #165315;
  cursor: pointer;
}
.view-details-btns-EachPlantComp p {
  color: #144d14;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
  text-align: center;
  margin-top: 0.5rem;
}
.BuyOnRent-EachPlantComp {
  display: flex;
  gap: 5px;
  align-items: center;
}
.BuyOnRent-EachPlantComp p {
  color: #165315;
  font-family: Inter;
  font-size: 13px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-decoration-line: underline;
  text-transform: uppercase;
}
.like-btn-EachPlantComp {
  position: absolute;
  display: flex;
  right: 0;
  margin-top: 1rem;
  margin-right: 1rem;
  cursor: pointer;
}

.images {
  display: flex;
  flex-direction: row;
  position: relative;
}

@media (min-width: 320px) and (max-width: 500px) {
  
  .inside-EachPlantComp {
    margin: 0;
    width: 178px;
  }
  

  .pic-plant--EachPlantComp{
    width: 180px;
    height: 235px
  }

  .btns-EachPlantComp {
   margin: 0 auto;
   margin-left:10px;
  }

  .view-details-btns-EachPlantComp {
    width: 130px;
    height: auto;
  }
  .display{
    display: none;
  }

  .view-details-btns-EachPlantComp a{
    font-size: 12px;
  }

  .view-details-btns-EachPlantComp p {
    font-size: 12px;
  }

  .pic-plant--EachPlantComp img {
    width: 150px;
    height: 180px;

  }

  .price-EachPlantComp p {

    font-size: 12px;
    font-weight: 400;
    max-width: 80px;
  }

  .price-EachPlantComp{
    max-width: 150px;
    /* width:150px; */
    height: 50px;

  }
}
