/* Footer.css */
/* Default styles for larger screens */

.footer-area {
  background-size: cover;
  background-position: center;
  text-align: center;
  background-color: #f0ffe5;
  color: #03370e;
  margin: 0;
  padding: 10px 0;
  width: 100%;
}

.footer-top {
  margin: 2rem;
  margin-bottom: 60px;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.col-lg-3,
.col-lg-2,
.col-md-4 {
  margin-bottom: 30px;
}

.footer-widget-logo img {
  width: 60px;
  height: 60px;
  margin-right: 10px;
}

.social-link ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.social-link li {
  margin-right: 10px;
}

.social-link a {
  color: #03370e;
  font-size: 20px;
  transition: color 0.3s ease;
}

.social-link a:hover {
  color: #10450f;
}

.footer-widget-title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 15px;
}

.footer-widget-list-item {
  list-style: none;
  margin: 0;
  padding: 0;
}

.footer-widget-list-item li {
  margin-bottom: 8px;
}

.footer-widget-list-item a {
  color: #10450f;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-widget-list-item a:hover {
  color: #10450f;
  font-size: 1.3rem;
}

.footer-contact-info {
  margin-bottom: 30px;
}

.pe-7s-call {
  font-size: 24px;
  margin-right: 10px;
  vertical-align: middle;
}

.footer-bottom {
  background-color: #f0ffe5;
  padding: 15px 0;
}

.footer-bottom .copyright-text {
  font-size: 14px;
  color: #03370e;
}

.footer-bottom a {
  color: #03370e;
  text-decoration: none;
}

.footer-bottom a:hover {
  color: #f3a939;
}

/* Media Query for Tablets */
@media (max-width: 991px) {
  .footer-top {
    margin: 1.5rem;
    margin-bottom: 40px;
  }

  .footer-widget-logo img {
    width: 50px;
    height: 50px;
    margin-right: 8px;
  }

  .footer-widget-title {
    font-size: 16px;
  }

  .social-link a {
    font-size: 18px;
  }

  /* Adjust other styles for tablet screens */
}

/* Media Query for Mobile Devices */
@media (max-width: 767px) {
  .footer-top {
    margin: 1rem;
    margin-bottom: 30px;
  }

  .footer-widget-logo img {
    width: 40px;
    height: 40px;
    margin-right: 6px;
  }

  .footer-widget-title {
    font-size: 14px;
  }

  .social-link a {
    font-size: 16px;
  }

  /* Adjust other styles for mobile screens */
}
