.button {
  background-color: #10450f;
  color: #f0ffe5;
  border: none;
  margin: 0.2rem;
  padding: 0.6rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
  border-radius: 5px;
}
.button:hover {
  background-color: white;
  color: #10450f;
  border: 0.3rem solid #10450f;
}

.arrival-bestseller-plants_imgs {
  min-width: 230px;
  width: 70%;
  height: 220px;
}

@media (min-width: 320px) and (max-width: 780px) {
  .arrival-bestseller-plants_imgs {
    width: 20%;
  }
}
