.cart-container {
  border: 2px solid rgb(172, 152, 152);
  padding: 1rem;
  width: 75%;
  margin: 0 auto;
  margin-bottom: 2rem;
}

.center {
  text-align: center;
}

.products_details {
  display: flex;
  width: 75%;
  padding: 0.75rem;
  align-items: center;
  justify-content: space-around;
  margin: 0 auto;
  /* border: 2px solid green; */
}
.headings {
  display: flex;
  width: 100%;
  /* border: 2px solid salmon; */
  align-items: center;
  justify-content: space-around;
}

.img_heading_box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* border: 2px solid green; */
}

.img_heading_box div img {
  margin-right: 15px;
}

.cart__item-img {
  width: 120px;
  height: 120px;
}

.br {
  /* border: 2px solid; */
}

.horizontalLine {
  width: 75%;
  height: 2px;

  background-color: rgb(230, 221, 221);
  margin: 10px auto;
}

.removeItemBtn {
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.cart_summary {
  display: flex;
  /* border: 2px solid red; */
  padding: 1.2rem;
  justify-content: space-around;
  align-items: center;
}

.cart_summary div {
  border: 2px solid rgb(230, 221, 221);
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cart_summary div p {
  margin-bottom: 0;
  font-weight: light;
  font-size: 20px;
}

.promo_text input {
  padding: 8px;
}

.action_btns {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.action_btns_col {
  display: flex;
  /* border: 2px solid; */
  width:60%;
  justify-content: space-around;
  /* padding: 15px; */
}

.action_btns_col button,
.action_btns button {
  font-weight: 500;
  padding: 0.6rem 1.4rem;
  color: white;
  background: #165315;
  border: none;
  border-radius: 0.8rem;
  transition: all 300ms ease-in;
  margin: 10px 0;
}

.action_btns_col button:hover,
.action_btns button:hover {
  cursor: pointer;
  transform: scale(1.1);
}

@media (min-width: 320px) and (max-width: 500px) {
  .cart-container {
    width: 100%;
  }
  .products_details {
    flex-direction: column;
    width: 100%;
  }

  .close_btn {
    position: relative;
    left: 130px;
    bottom: 164px;
  }
  .action_btns_col{
    /* width: 100%; */
    flex-direction: column;
  }
}
