.hero-container {
  padding: 20px;
  background-color: #fee3d7;
  color: #10450f;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  margin: 0 auto;
  max-width: 99%;
  height: 35rem;
}

.hero-row {
  display: flex;
  height: 100%;
}

.hero-col-left {
  display: flex;

  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.hero-col-right {
  padding: 1, 5rem;
  width: 100%;
}

h1 {
  font-size: 28px;
  margin-bottom: 10px;
}

.p {
  font-size: 16px;
  margin-bottom: 20px;
}

.slider-image {
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
}

.btn-container-hero {
  padding: 5rem;
}

.btn-hero {
  background-color: #10450f;
  color: #f0ffe5;
  border: none;
  margin: 1rem;
  padding: 1rem 2rem;
  border-radius: 1rem;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s ease;
}

.btn-hero:hover {
  background-color: white;
  color: #10450f;
  border: 0.3rem solid #10450f;
  border-radius: 1rem;
}

.hero-section__banner {
  width: "100%";
}

.common-overlay-btn {
  position: absolute;
  top: 62%;
  left: 8%;
  transform: translate(-50%, -50%);
  background-color: #f8fef0;
  padding: 0.75rem 1.5rem;
  border: 2px solid #165316;
  border-radius: 1rem;
}

.overlay-button_2 {
  top: 83%;
  left: 8%;
  background-color: #165316;
  color: white;
}

.overlay-button_3 {
  top: 80%;
  left: 80%;
}

.carousel-custom_style{
  width: 5% !important;
}

@media (min-width: 320px) and (max-width: 500px) {
  .common-overlay-btn {
    padding: 0px 5px;
    font-size: 8px;
    left: 12%;
    top: 65%;
  }
  .overlay-button_2 {
    top: 87%;
    left: 12%;
  }
  .overlay-button_3 {
    top: 85%;
    left: 80%;
    padding:3px 8px;
  }

}

