
/* signup form */
.signup-container{
        width: 100vw;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
}

.signup-form{
    padding: 45px;
    width: 50%;
     /* border: 2px solid; */
    margin: 0 auto;
}

.logo{
    width: 250px;
}



/* login form */

.login-container{
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.login-form {
    padding: 45px;
    width: 50%;
     /* border: 2px solid; */
    margin: 0 auto;
}

@media (min-width:320px) and (max-width:500px){
    .signup-container,.login-container{
        width: 100%;
        height: 100%;
    }
    .signup-form,.login-form{
        width: 100%;
    }

    .logo{
        width: 150px;
    }
}