/* Reset some default styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.contact-us {
  max-width: 100%;
  margin: 0 auto;
  padding: 30px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.contact-us h2 {
  margin-top: 0;
  font-size: 24px;
}

.contact-heading {
  text-align: center;
}

.contact-image {
  display: block;
  margin: 20px auto;
  max-width: 50%;
  height: 30rem;
  border-radius: 5px;
  border-radius: 1.5rem;
}

.contact-us p {
  margin-bottom: 15px;
  line-height: 1.5;
}

.contact-form {
  margin-bottom: 30px;
  padding: 30px;
  width: 80%;
}

.contact-form label {
  font-weight: bold;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 14px;
}

#comment {
  background-color: #f0ffe5;
}

.contact-form button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 1rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.contact-form button:hover {
  background-color: white;
  color: #007bff;
  border: 0.3rem solid #007bff;
  border-radius: 1rem;
}

.contact-details {
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.contact-details h3 {
  font-size: 20px;
  margin-bottom: 15px;
}

.contact-details p {
  font-size: 14px;
  line-height: 1.5;
  margin-bottom: 10px;
}

.google-maps {
  margin-top: 30px;
  border-radius: 10px;
  overflow: hidden;
}

.google-maps iframe {
  width: 100%;
  height: 400px;
  border: none;
}

/* Responsive styles */
@media (max-width: 768px) {
  .contact-us {
    padding: 20px;
  }

  .contact-form input,
  .contact-form textarea {
    font-size: 16px;
  }

  .contact-details {
    padding: 15px;
    margin-top: 20px;
  }

  .google-maps iframe {
    height: 300px;
  }
}
