

.main-container {
  margin: 1rem;
  padding: 1rem;

}

.box-container {
  /* border: 2px solid; */
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scrollbar-width: none;
  justify-content: space-around;
  width: 60%;
  margin: 0 auto;
}

.box-container img {
  border-radius: 80%;
  border: 0.6rem solid #10450f;
  height: 100px;
  width: 100px;
}

.box-container::-webkit-scrollbar {
  display: none; /* Safari and Chrome */
}

.col-IconImg {
  /* border: 2px solid green; */
  /* width: 100%; */
}


@media (min-width:320px) and (max-width:500px){
  .box-container{
    flex-wrap: wrap;
    width: 100%;
    justify-content: flex-start;
  }

  .col-IconImg{
    padding: 15px;
    width: 150px;
    margin: 0 auto;
  }

}



