/* Header styles */
.navbar {
  background-color: white !important; /* Set your desired background color */
}

.navbar-brand img {
  max-width: 100px; /* Set the maximum width for the logo */
  height: auto; /* Maintain aspect ratio */
}

.navbar-nav {
  margin: auto; /* Center-align navigation items */
}

.navbar-nav .nav-item {
  margin-right: 15px; /* Adjust the margin between navigation items */
}

.nav-item:hover {
  color: #165315;
  font-size: 1.1rem;
}

.nav-link {
  color: #343a40; /* Set the text color for navigation links */
}

.nav-link:hover {
  color: #165315; /* Set the text color for navigation links on hover */
}

.dropdown-menu {
  background-color: #fff; /* Set the background color for the dropdown menu */
}

.dropdown-item {
  color: #343a40; /* Set the text color for dropdown items */
}

.dropdown-item:hover {
  background-color: #165315; /* Set the background color for dropdown items on hover */
  color: #fff; /* Set the text color for dropdown items on hover */
}

.form-control {
  border-color: #ced4da; /* Set the border color for form controls */
}

.btn-outline-success {
  color: #28a745; /* Set the text color for the outline success button */
  border-color: #28a745; /* Set the border color for the outline success button */
}

.btn-outline-success:hover {
  background-color: #28a745; /* Set the background color for the outline success button on hover */
  color: #f3a939; /* Set the text color for the outline success button on hover */
}

.menu-icon {
  display: none;
}

.search {
  width: 40.9375rem;
}

.search input {
  background-color: #f0ffe5;
  border-radius: 0px;
}
#search-icon {
  border-radius: 0px 0.9375rem 0.9375rem 0px;
  background: #f0ffe5;
}
#search-icon {
  background: #f0ffe5;
}

#category {
  border-radius: 0.9375rem 0rem 0rem 0.9375rem !important;
  background: #165315 !important;
  color: #fff;
}

.mobile-visible {
  display: none;
}

@media (min-width: 320px) and (max-width: 500px) {
  .mobile-visible {
    display: inline-block;
  }
}

/* cart badge style */
.cart-icon {
  position: relative;
  display: inline-block;
}

.fa-shopping-cart {
  font-size: 24px;
  color: #333; /* Change the color as needed */
}

.badge {
  position: absolute;
  top: -10px;
  right: -10px;
  background-color: #f00; /* Badge background color */
  color: #fff; /* Badge text color */
  border-radius: 50%;
  padding: 4px 8px;
  font-size: 14px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .menu-icon {
    display: block;
  }

  .navbar-nav .nav-item {
    margin-right: 10px; /* Adjust the margin between navigation items for smaller screens */
  }
}
