

.plants_container {
  /* border: 2px solid; */
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.section_heading h2 {
  text-align: center;
  margin: 30px 0;
}

.plant_container {
  /* border: 2px solid green; */

  padding: 1.25rem;
  margin: 1.8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background-color: #ffffff;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border-radius: 4px;
  min-width: 400px;
}

.plant_container:hover {
  transform: translateY(-5px);
  box-shadow: 0px 8px 12px rgba(0, 0, 0, 0.2);
}

.plant_img {
  width: 100%;
  height: 220px;
}

.action_btns {
  display: flex;
  margin-top: 1.2rem;
  gap: 1rem;
}

.action_btns button {
  background-color: #165315;
  color: white;
  padding: 0.6rem;
  border: none;
  border-radius: 7px;
  cursor: pointer;
}

.action_btns button:hover {
  background-color: white;
  color: #165315;
  border: 0.2rem solid #165315;
}



@media (min-width: 320px) and (max-width: 500px) {
  .plants_container {
    gap:0px;
    width: 100%;
    justify-content: enter;
    align-items: enter;
     margin: 0 auto;
     margin-left: 12px;
  }
}
@media (min-width: 600px) and (max-width: 1150px) {
  .plants_container {

    width: 100%;
    margin: 0;
  }
}
