.parentContainer{
    width: 75%;
    margin: 30px auto;
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* border: 2px solid ; */

}

.infoContainer{
    /* border: 2px solid green; */
    padding: 1rem;
}

.startingPrice{
    border: 1px solid;
    padding: 0.4rem;
    width:180px;
    border-radius: 10px;
    margin-bottom: 0.75rem;
}

.mediaContainer{
    /* border: 2px solid red; */
}

.mediaContainer img{
    width: 30rem;
}



.actions_btns{
    /* border: 2px solid; */
    width: 50%;
    display: flex;
    margin-top: 2rem;
    align-items: center;
    justify-content: space-around;
}

.book-your-slot_btn{
    padding: 1rem;
    color: white;
    background-color: #144D14;
    border-radius: 15px;
    cursor: pointer;
}

.book-your-slot_btn:hover{
    color: #144D14;
    background-color: white; 
    font-weight: bold;
}

.learn-more_btn{
color: #144D14;
border: none;
font-weight: bold;
font-size: 1.3rem;
background: transparent;
}


@media (min-width:320px) and (max-width:500px){
    .parentContainer{
        width: 100%;
        flex-direction: column;
    }

    .actions_btns{
        width: 100%;
    }

    .mediaContainer img{
        width: 100%;
    }
}